@font-face {
  font-family: BPDots;
  src: url("BPdotsSquareVF.25fec6ee.ttf") format("truetype");
  font-display: swap;
}

@font-face {
  font-family: ZxGamut;
  src: url("ZxGamut-Variable.6528b92f.woff2") format("woff2");
  font-display: swap;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  background: #52a3cc;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: ZxGamut, monospace;
  overflow-x: hidden;
}

.app {
  z-index: 10;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  min-height: 100vh;
  padding: 8rem 2.5rem 2.5rem 2rem;
  display: flex;
  position: relative;
  overflow-y: auto;
}

#p5-background-container {
  z-index: 1;
  width: 100vw;
  height: 48px;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

#p5-background {
  background-color: #52a3cc;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

#p5-background canvas {
  opacity: 0;
  backface-visibility: hidden;
  transition: opacity 1.5s ease-in-out;
  display: block;
  transform: translateZ(0);
}

#p5-background canvas.fade-in {
  opacity: 1;
}

#background-video {
  object-fit: cover;
  z-index: 0;
  width: 100%;
  height: 100%;
  image-rendering: pixelated;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
  position: fixed;
  top: 0;
  left: 0;
}

.landing-container {
  text-align: center;
  width: 100%;
  max-width: 90vw;
  margin-bottom: 2rem;
}

.site-header {
  backdrop-filter: blur(4px);
  z-index: 100;
  background: #ffffff80;
  border-bottom: 1px solid #fff3;
  width: 100%;
  margin-bottom: 2rem;
  padding: 1.5rem 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.header-content {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 5rem;
  max-width: 90vw;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
}

.header-left {
  align-items: flex-end;
  gap: 1rem;
  margin-top: .3rem;
  display: flex;
}

.cloud-icon {
  width: auto;
  height: 3rem;
  margin-bottom: .3rem;
  display: block;
}

.header-title {
  color: #0d1b33;
  white-space: nowrap;
  margin: 0;
  font-family: BPDots, monospace;
  font-size: 3rem;
  font-weight: 900;
}

.header-tagline {
  color: #0d1b33;
  white-space: nowrap;
  font-family: ZxGamut, monospace;
  font-size: 1.3rem;
}

.intro-section {
  margin-bottom: 2rem;
  overflow: hidden;
  padding: 0 !important;
}

.intro-content {
  align-items: center;
  gap: 0;
  max-height: 300px;
  display: flex;
}

.intro-image-placeholder {
  aspect-ratio: 16 / 9;
  color: #0d1b3399;
  background: #ffffff1a;
  border: 1px solid #ffffff4d;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 100%;
  font-family: ZxGamut, monospace;
  display: flex;
}

.intro-image {
  object-fit: cover;
  border-radius: 0;
  flex-shrink: 0;
  width: 50%;
  height: 100%;
  display: block;
}

.intro-text {
  flex: 1;
  align-items: center;
  padding: 2rem;
  display: flex;
}

.intro-text p {
  margin: 0;
  line-height: 1.4;
}

.workshops-container {
  margin-bottom: 2rem;
}

.workshops-grid {
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  display: grid;
}

.workshop-box {
  background: #ffffff80;
  border: 1px solid #fff3;
  border-radius: 8px;
  flex-direction: column;
  padding: 0;
  display: flex;
  overflow: hidden;
}

.workshop-image-placeholder {
  aspect-ratio: 16 / 9;
  color: #0d1b3399;
  background: #ffffff1a;
  border-bottom: 1px solid #ffffff4d;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  font-family: ZxGamut, monospace;
  display: flex;
}

